import React, { Component } from 'react';

export class Title extends Component{
    constructor(props, context){
        super(props, context);
        
        this.state = {
            data : 'this is constructor'
        };
    }
    
    
    render (){
        return (
          React.createElement("div", null, 
            React.createElement("h1", null, "Hello React~!"), 
            React.createElement("h2", null, "Author"), 
            React.createElement("h3", null, this.props.text), 
            React.createElement("hr", null), 
            React.createElement("h1", null, this.props.text1, " by ", this.props.author), 
            React.createElement("h2", null, this.state.data)
          )
        );
    }
};

Title.defaultProps = {
    text1 : 'Hello',
    author : 'Ract'
}